import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import onlineTalk from '@/components/online_talk'
import checkWork from '@/components/check_work'
import resource from '@/components/resource'
import students from '@/components/students'
import tests from '@/components/tests'
import majors from '@/components/majors'

Vue.use(Router)

export default new Router({
  routes: [
        {
          path: '/',
          component: login
        },
      {
          path: '/online_talk',
          component: onlineTalk,
          meta: {
              check_login: true
          }
      },
      {
          path: '/check_work',
          component: checkWork,
          meta: {
              check_login: true
          }
      },
      {
          path: '/resource',
          component: resource,
          meta: {
              check_login: true
          }
      },
      {
          path: '/students',
          component: students,
          meta: {
              check_login: true
          }
      },
      {
          path: '/tests',
          component: tests,
          meta: {
              check_login: true
          }
      },
      {
          path: '/majors',
          component: majors,
          meta: {
              check_login: true
          }
      },
  ]
})



