<template>
    <span :class="{'head-widget':true, 'h-w-green':color}">
        <span class="h-w-number" v-if="number">{{number}}</span>
        <span>{{short_uname}}</span>
    </span>
</template>

<script>
    export default {
        name: "head-widget",
        props: ["uname", "color", "number"],
        computed: {

            /*显示名字最后2个字*/
            short_uname() {
                return this.uname.substr(-2, 2);
            },

        },
    }
</script>

<style scoped>
    @import "../../css/head-widget.css";
</style>


