<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">教师端</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <h2 class="base-title-widget bt-w-blue">在线答疑</h2>
                    <hr class="seg-widget"/>
                    <div style="padding: 1.4rem 2rem;">
                        <tab-widget :tabs="tabs"></tab-widget>
                    </div>
                    <editor-widget :mail-list="classmate"></editor-widget>
                    <div class="pad-h-2"></div>
                </div>
                <div class="pad-h-4"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Editor from "@/components/widget/editor-widget";
    import Head from "@/components/widget/head-widget";
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "online_talk",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "在线答疑",
                        act: "act",
                        type: "route",
                        link: "/online_talk",
                    },
                    {
                        name: "作业批改",
                        type: "route",
                        link: "/check_work",
                    },
                    {
                        name: "资源管理",
                        type: "route",
                        link: "/resource",
                    },
                    {
                        name: "学生管理",
                        type: "route",
                        link: "/students",
                    },
                    {
                        name: "试题管理",
                        type: "route",
                        link: "/tests",
                    },
                    {
                        name: "课程管理",
                        type: "route",
                        link: "/majors",
                    }
                ],

                /*tab-widget*/
                tabs: [
                    {
                        name: "学生",
                        state: "act",
                    },
                    {
                        name: "小组",
                    },
                ],

                /*通讯同学*/
                classmate: [],

            }
        },
        components: {
            "tab-widget": Tab,
            "editor-widget": Editor,
            "head-widget": Head,
            "aside-nav-widget": AsideNav,
        },
        mounted(){
            /*this.$axios.get("classmate/")
                .then(res => {
                    let classmate = res.data;
                    this.classmate = this.$B.data_convert(classmate, {
                        uid: "id",
                        state: "",
                        name: "name",
                    });
                })*/
        },
    }
</script>

<style scoped>
    @import "../css/seg-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
</style>


