<template>
    <div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">教师端</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <!--搜索栏组件-->
                <div class="pannel-widget pad-w-2">
                    <div class="search-widget">
                        <div class="s-w-inp">
                            <i class="s-w-inp-ico icon-sousuo"></i>
                            <input class="s-w-inp-text" type="text" placeholder="学员搜索 ..."/>
                        </div>
                        <span class="s-w-btn" @click="search">搜索</span>
                    </div>
                </div>

                <div class="pad-h-2"></div>

                <div class="pannel-widget">
                    <h2 class="base-title-widget bt-w-blue">学生管理</h2>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>

                    <!--排行榜组件-->
                    <div class="base-table-widget">
                        <table class="bt-w-table">
                            <thead>
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">姓名</td>
                                <td class="bt-w-title-td pcenter">进度</td>
                                <td class="bt-w-title-td pcenter">当前任务</td>
                                <td class="bt-w-title-td pcenter">小组</td>
                                <td class="bt-w-title-td pcenter">周进度</td>
                                <td class="bt-w-title-td pcenter">月进度</td>
                                <td class="bt-w-title-td pcenter">操作</td>
                            </tr>
                            </thead>
                            <tbody>
                            <tr class="bt-w-tr" v-for="(student, index) of students">
                                <td class="bt-w-td">
                                    <head-widget :uname="student.name"></head-widget>
                                    <span>{{student.name}}</span>
                                </td>
                                <td class="bt-w-td pcenter">{{student.progress}}</td>
                                <td class="bt-w-td pcenter">{{student.tesk}}</td>
                                <td class="bt-w-td pcenter">{{student.team}}</td>
                                <td class="bt-w-td pcenter">{{student.week_progress}}</td>
                                <td class="bt-w-td pcenter">{{student.month_progress}}</td>
                                <td class="bt-w-td pcenter"><span>以学生身份访问</span></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>

                    <!--页码组件-->
                    <div class="page-widget" style="margin-bottom: 1rem;">
                        <a class="p-w-btn" href="#"><i class="icon-arrow_left"></i></a>
                        <span class="p-w-number"><b class="p-w-cur">{{article_pages.cur_page}}</b>/<small class="p-w-total">{{article_pages.total_pages}}</small></span>
                        <a href="#" class="p-w-btn"><i class="icon-arrow_right"></i></a>
                    </div>

                </div>

                <div class="pad-h-4"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget";
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "students",
        data(){
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "在线答疑",
                        type: "route",
                        link: "/online_talk",
                    },
                    {
                        name: "作业批改",
                        type: "route",
                        link: "/check_work",
                    },
                    {
                        name: "资源管理",
                        type: "route",
                        link: "/resource",
                    },
                    {
                        name: "学生管理",
                        type: "route",
                        act: "act",
                        link: "/students",
                    },
                    {
                        name: "试题管理",
                        type: "route",
                        link: "/tests",
                    },
                    {
                        name: "课程管理",
                        type: "route",
                        link: "/majors",
                    }
                ],

                /*学员列表*/
                students: [
                    {
                        name: "小明",
                        progress: "慢（1周）",
                        tesk: "1-1-2",
                        team: "大数据01",
                        week_progress: "",
                        month_progress: "",
                    }
                ],

                /*分页*/
                article_pages: {
                    cur_page: 1,
                    total_pages: 12
                }

            }
        },
        methods: {

            /*搜索*/
            search(){}

        },
        components: {
            "head-widget": Head,
            "aside-nav-widget": AsideNav,
        },
    }
</script>

<style scoped>
    @import "../css/seg-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
    @import "../css/search-widget.css";
    @import "../css/base-table-widget.css";
    @import "../css/page-widget.css";
</style>


